@import url("https://fonts.googleapis.com/css?family=Montserrat");

:root {
    --main-color: #f8e856;
}

.fixed-logo {
    display: none;
    position: fixed;
    top: 2%;
    height: 170px;
    aspect-ratio: 1 / 1;
    z-index: 1000;
    right: 25px;
    top: 10px;

    font-family: "Android 101" !important;
}
@media (max-width: 600px) {
    .fixed-logo {
        height: 150px;
    }
}
.fixed-logo-visible {
    display: unset;
}

.pback {
    background-image: url("./background.jpg");
    height: 100vh;
    width: 100vw;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
h3 {
    margin: 10px 0;
}

h6 {
    margin: 5px 0;
    /* text-transform: uppercase; */
}

p {
    font-size: 14px;
    line-height: 21px;
}

.containers {
    position: absolute;
    width: 300px;
    height: 200px;
    z-index: 15;
    top: 50%;
    left: 50%;
    margin: -100px 0 0 -150px;
}

.card-container {
    background-color: #231e39;
    border-radius: 5px;
    box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.75);
    color: #b3b8cd;
    padding-top: 30px;
    position: relative;
    width: 350px;
    max-width: 100%;
    text-align: center;
    align-items: center;
    padding-bottom: 30px;
    padding-left: 5px;
    padding-right: 5px;
}

.card-container .pro {
    color: #231e39;
    background-color: #febb0b;
    border-radius: 3px;
    font-size: 14px;
    font-weight: bold;
    padding: 3px 7px;
    position: absolute;
    top: 30px;
    left: 30px;
}

.card-container .round {
    border: 1px solid #03bfcb;
    border-radius: 50%;
    padding: 7px;
}

button.primary {
    background-color: #03bfcb;
    border: 1px solid #03bfcb;
    border-radius: 3px;
    color: #231e39;
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    padding: 10px 25px;
}

button.primary.ghost {
    background-color: transparent;
    color: #02899c;
}

.skills {
    background-color: #1f1a36;
    text-align: left;
    padding: 15px;
    margin-top: 30px;
}

.skills ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.skills ul li {
    border: 1px solid #2d2747;
    border-radius: 2px;
    display: inline-block;
    font-size: 12px;
    margin: 0 7px 7px 0;
    padding: 7px;
}

footer {
    background-color: #222;
    color: #fff;
    font-size: 14px;
    bottom: 0;
    position: fixed;
    left: 0;
    right: 0;
    text-align: center;
    z-index: 999;
}

footer p {
    margin: 10px 0;
}

footer i {
    color: red;
}

footer a {
    color: #3c97bf;
    text-decoration: none;
}

.logout-button {
    position: absolute;
    top: 50px;
    left: 100px;
    color: black;
    background-color: #f8e856;
    padding: 10px 15px;
    border-radius: 10000px;
    -webkit-border-radius: 10000px;
    -moz-border-radius: 10000px;
    -ms-border-radius: 10000px;
    -o-border-radius: 10000px;
}

.logout-button:hover {
    cursor: pointer;
}

.loading {
  height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;

}
